.bracket {
    margin-top:30px;
    width: 100%;
}

.circle {
    position: absolute;
    margin: auto;
    border: 1px solid grey;
    border-radius:50%;
    transform:translate(-50%,-50%);
    left:50%;
    top:50%;
    overflow:hidden;
    background-color:white;

}

.wrapper {
    /* width: 100%; */
    /* padding-bottom: 100%; */
    width: 900px;
    height: 900px;
    margin: auto;
    position: relative;
}

.circle-wrapper {
    position:absolute;
    width:100%;
    padding-bottom:100%;
}

.team1 {
    position: absolute;
    width: 25px;
    height: 25px;
    top: 50%;
    left: 50%;
    z-index:10;
}

.team4 {
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    z-index:10;
}

.team1:hover {
    cursor: pointer;
}
.team4:hover {
    cursor: pointer;
}

.segment {
    width:100%;
    padding-bottom:100%;
    overflow:hidden;
    position:absolute;
    transform: translate(0,-50%) rotate(90deg) rotate(calc(var(--offset,0) * 1deg));
    transform-origin:50% 100%;
    border-bottom: 1px solid grey;
    pointer-events: none;
}

.segment::before {
    /* background: #f00; */
    /* border-top: 1px solid red; */
    content: '';
    position:absolute;
    height:100%;
    /* padding-bottom: 100%; */
    transform: translate(-50%, 100%) rotate(calc(var(--value, 45) * 1deg));
    transform-origin: 50% 0;
    width: 100%;
    pointer-events: none;
}

.detail {
    min-width: 40px;
    min-height: 40px;
    padding: 4px;
}

.win-table {
    padding: 0;
    margin: 0;
}
.win-table > li{
    display: block;
}

.toolbar {
    padding: 4px;
}

.footer {
    padding: 4px;
    bottom: 0;
}

@media print {
   .toolbar {
       display: none;
   }

   .footer {
       position: fixed;
       left: 0;
       right: 0;
       bottom: 0;
   }
}

.divisions {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.division-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.division-col {
    padding: 4px;
    font-size: 40px;
    font-weight: bold;
    color: grey;
}