body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bracket {
    margin-top:30px;
    width: 100%;
}

.circle {
    position: absolute;
    margin: auto;
    border: 1px solid grey;
    border-radius:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    left:50%;
    top:50%;
    overflow:hidden;
    background-color:white;

}

.wrapper {
    /* width: 100%; */
    /* padding-bottom: 100%; */
    width: 900px;
    height: 900px;
    margin: auto;
    position: relative;
}

.circle-wrapper {
    position:absolute;
    width:100%;
    padding-bottom:100%;
}

.team1 {
    position: absolute;
    width: 25px;
    height: 25px;
    top: 50%;
    left: 50%;
    z-index:10;
}

.team4 {
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    z-index:10;
}

.team1:hover {
    cursor: pointer;
}
.team4:hover {
    cursor: pointer;
}

.segment {
    width:100%;
    padding-bottom:100%;
    overflow:hidden;
    position:absolute;
    -webkit-transform: translate(0,-50%) rotate(90deg) rotate(calc(0 * 1deg));
            transform: translate(0,-50%) rotate(90deg) rotate(calc(0 * 1deg));
    -webkit-transform: translate(0,-50%) rotate(90deg) rotate(calc(var(--offset,0) * 1deg));
            transform: translate(0,-50%) rotate(90deg) rotate(calc(var(--offset,0) * 1deg));
    -webkit-transform-origin:50% 100%;
            transform-origin:50% 100%;
    border-bottom: 1px solid grey;
    pointer-events: none;
}

.segment::before {
    /* background: #f00; */
    /* border-top: 1px solid red; */
    content: '';
    position:absolute;
    height:100%;
    /* padding-bottom: 100%; */
    -webkit-transform: translate(-50%, 100%) rotate(calc(45 * 1deg));
            transform: translate(-50%, 100%) rotate(calc(45 * 1deg));
    -webkit-transform: translate(-50%, 100%) rotate(calc(var(--value, 45) * 1deg));
            transform: translate(-50%, 100%) rotate(calc(var(--value, 45) * 1deg));
    -webkit-transform-origin: 50% 0;
            transform-origin: 50% 0;
    width: 100%;
    pointer-events: none;
}

.detail {
    min-width: 40px;
    min-height: 40px;
    padding: 4px;
}

.win-table {
    padding: 0;
    margin: 0;
}
.win-table > li{
    display: block;
}

.toolbar {
    padding: 4px;
}

.footer {
    padding: 4px;
    bottom: 0;
}

@media print {
   .toolbar {
       display: none;
   }

   .footer {
       position: fixed;
       left: 0;
       right: 0;
       bottom: 0;
   }
}

.divisions {
    width: 100%;
    height: 100%;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.division-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.division-col {
    padding: 4px;
    font-size: 40px;
    font-weight: bold;
    color: grey;
}
